/**
 * @description: 
 * @param: moduleId 
 * @param: key 
 * @return:  true
 */
export function beforeTableEvent(props, moduleId, key, value, index, record, status) {
    return new Promise((resolve, reject)=>{
        if (key === "fieldcode") {
            let meta = props.meta.getMeta();
            meta[moduleId].items.find((item) => {
                if ('fieldcode' === item.attrcode) {
                    // 
                    let classid=props.form.getFormItemsValue("card_head","midmetadata").value;
                    // 
                    item.queryCondition = {
                        classid:classid
                        // // SQL"TreeRefActionExt" : "GridRefActionExt"
                        // GridRefActionExt: 'nccloud.web.hpf.sumrulevo.refsqlbuilder.MdEntityRefSqlBuilder',
                    };
                }
            });
            // 
            props.meta.setMeta(meta, () => {
                // meta
                resolve(true);
            });
        }
        else {
            resolve(true);
        }
    });

}
