    meta[CARD.form_id].items.map(item => {
        if (item.attrcode === FIELD.org) { //
            item.queryCondition = () => ({
                    funcode: props.getSearchParam('c')//appcode
            });
        }
    });

    // suqc    - 
    let idissys = this.props.getUrlParam(FIELD.idissys);//
    let idissysbilltype = this.props.getUrlParam(FIELD.idissysbilltype);//
    if (idissys == 8 && idissysbilltype == '0001ZZZZ5C204CC569DD') {//
        meta[CARD.table_code].items.map((item, key) => {
            if (item.attrcode == 'ifchange') {//
                item.visible = false;
            }
            return item;
        });
    }

    
    // for (let item of Object.keys(meta.gridrelation)) {
