export function pageInfoClick(props, config, pks) {
    let data = {
        pks,
        pagecode: LIST.page_code
    };

    ajax({
        url: REQUEST_URL.queryListByPks,
        data,
        success: (res) => {
            let { success, data } = res;
            if (success && data && data[LIST.table_id]) {
                props.table.setAllTableData(LIST.table_id, data[LIST.table_id]);
                toast({ color: STATUS.success });
            } else {
                props.table.setAllTableData(LIST.table_id, { rows: [] });
                toast({ color: STATUS.warning, content: props.json['M0Z10301-000010'] });/*  */
            }
        }
    });
}

/**
 * 
 * @param {*} res           response
 * @param {*} opername      
 */
export function PromptMessage(res) {
    let { status, msg } = res.data;
    let content;
    let total = res.data.total;
    let successNum = res.data.successNum;
    let failNum = res.data.failNum;
    content = '' + '' + total + '';/*  ,*/
    content = content + ',' + successNum + '';/*  , ,,*/
    content = content + ',' + failNum + ',';/*  ,,*/
    let errMsgArr = res.data.errormessages;
    //
    if (status == 0) {
        toast({
            color: "success",
            title: '' + msg,
            content,
            TextArr: ['', '', ''],/*  ,,*/
            groupOperation: true
        });
    }
    //
    else if (status == 1) {
        toast({
            duration: "infinity",
            color: "danger",
            title: '' + msg,
            content,
            TextArr: ['', '', ''],/*  ,,*/
            groupOperation: true,
            groupOperationMsg: errMsgArr
        });
    }
    //
    else if (status == 2) {
        toast({
            duration: "infinity",
            color: "warning",
            title: '' + msg,
            content,
            TextArr: ['', '', ''],/*  ,,*/
            groupOperation: true,
            groupOperationMsg: errMsgArr
        });
    }
}

