import { editcfg } from '../../constant';


/**
 * @description: 
 * @param: moduleId 
 * @param: key 
 */
export function afterTableEvent(props, moduleId, key, value, changedrows, index, record, type, method) {
    
    //
    if(key=="fieldcode"){
        props.cardTable.setValByKeyAndIndex(moduleId, index, "fieldid", { value: value.refpk });
        props.cardTable.setValByKeyAndIndex(moduleId, index, "fieldname", { value: value.refname });
        props.cardTable.setValByKeyAndIndex(moduleId, index, "fieldcode", { value: value.refcode });
        // add by mzq    
        let idissysbilltype = props.getUrlParam('idissysbilltype');
         
        if (editcfg.get(idissysbilltype).indexOf(value.values.code.value) > -1) {
            if(value.values.code.value=="cydeptcode"||value.values.code.value=="cydeptname"){
                props.cardTable.setValByKeyAndIndex(moduleId, index, "ifchange", { value: true });             

            }else{
                props.cardTable.setValByKeyAndIndex(moduleId, index, "ifchange", { value: false });             

            }
            //props.cardTable.setValByKeyAndIndex(moduleId, index, "ifchange", { value: true });             
            props.cardTable.setEditableByIndex('card_body', index, ['fieldcode', 'fieldname', 'ifchange'], false);

            }
          
    }
}
