import {FIELD, CARD_ADD_DISABLED_BUTTON, CARD, REQUEST_URL, STATUS, PRIMARY_KEY, DATASOURCE} from "../../constant";
import {initData} from "../../list/events/listOperator";
import { promptBox,ajax } from 'nc-lightapp-front';
import {getRowNo} from "./bodyButtonClick";

/**
 * @description: 
 * @param: moduleId 
 * @param: key 
 */
export function afterHeadEvent(props, moduleId, key, value, oldValue) {
    if (key === FIELD.org) {
        props.resMetaAfterPkorgEdit(); //
        props.button.setButtonDisabled(CARD_ADD_DISABLED_BUTTON, false);//
        props.cardTable.setAllCheckboxAble(CARD.table_code, true);

        if(oldValue.value&&value.value!=oldValue.value){
            //
            promptBox({
                color: 'info',
                title: '',
                content: ``,
                noFooter: false,
                noCancelBtn: false,
                beSureBtnName: '',
                cancelBtnName: '',
                hasCloseBtn: false,
                beSureBtnClick: ()=>{
                    //
                    props.form.setFormItemsValue(CARD.form_id, {
                        idissys:{value:null,display:null},
                        idissysbilltype:{value:null,display:null},
                        midmetadata:{value:null,display:null}
                    });
                    //
                    clearTable(props);
                },
                cancelBtnClick:()=>{
                    //
                    props.form.setFormItemsValue(CARD.form_id, {
                        pk_org:{value:oldValue.value,display:oldValue.display}
                    });
                },
                closeByClickBackDrop: false
            });
        }
    }
    if(key=="idissys"){
        if(value.value&&value.value!=oldValue.value){
            //
            props.form.setFormItemsValue(CARD.form_id, {
                idissysbilltype:{value:null,display:null},
                midmetadata:{value:null,display:null}
            });
            //
            clearTable(props);
        }
    }
    if(key=="idissysbilltype"){
        if(value.value&&value.value!=oldValue.value){
            //
            props.form.setFormItemsValue(CARD.form_id, {
                midmetadata:{value:null,display:null}
            });
            //
            clearTable(props);
        }
    }
    if(key=="midmetadata"){
        if(value.value&&value.value!=oldValue.value){
            //
            this.setState({showLoading: true});

            let tableid=value.values.defaulttablename.value;
            //--
            hideTableCol(props);
            //
            clearTable(props);
            //
            ajax({
                url: REQUEST_URL.queryColumn,
                data: {tableid:tableid},
                success: (res) => {
                    let rowno=getRowNo(props);
                    let gridData=[];
                    res.data.forEach(data=>{
                        gridData.push({
                            values:{
                                rowno:{display:rowno,value:rowno.toString()}, //
                                fieldid:{display:data.fieldid,value:data.fieldid}, //id
                                fieldcode:{display:data.fieldcode,value:data.fieldcode}, //
                                fieldname:{display:data.fieldname,value:data.fieldname} //
                            }
                        });
                        rowno+=10;
                    });
                    props.cardTable.insertRowsAfterIndex(CARD.table_code,gridData,-1);
                    //
                    this.setState({showLoading: false});
                },
                error:(error)=>{
                    console.log(error);
                    //
                    this.setState({showLoading: false});
                }
            });
        }
    }
}

/**
 * 
 * */
function clearTable(props){
    let rows=props.cardTable.getAllRows(CARD.table_code);
    rows.forEach(row=>{
        props.cardTable.delRowByRowId(CARD.table_code, row.rowid);
    });
}

function hideTableCol(props){
    let [idissys, idissysbilltype] = props.form.getFormItemsValue(CARD.form_id, [FIELD.idissys, FIELD.idissysbilltype]);//
    let meta = props.meta.getMeta();
    meta[CARD.table_code].items.map((item, key) => {                    
        if(item.attrcode == 'ifchange'){
            if(idissys.value == '8' && idissysbilltype.refcode == 'CO12'){
                item.visible = false;
            } else {
                item.visible = true;
            }
        }
        return item;
    });
    props.meta.setMeta(meta);
}